import React, { useState } from 'react';
import {Thumbnail, LikeButton} from 'react';
import "../BirthdayWish.css"; // Import the CSS file with additional styles
import { Link } from "react-router-dom";


const BirthdayWish = ({birthday}) => {
    const [isHovered, setIsHovered] = useState(false);

    const handleHover = () => {
      setIsHovered(true);
    };
  
    const handleHoverEnd = () => {
      setIsHovered(false);
    };

      return (
        
        <div 
        className={`box ${isHovered ? "hovered" : ""}`} // Add "hovered" class when component is hoveredstyle={boxStyle}
        onMouseEnter={handleHover}
        onMouseLeave={handleHoverEnd}
        style={{
          "--image-default": `url()`,
          "--image-hover": `url('/assets/hollow-knight-god-home.gif')`,
        }}
        >
            
            <a href={`wish/${birthday.name}`} style={{ display: "block", width: "100%", height: "100%", textDecoration: "none" }}>
             <h3 style={{ color: 'white' }}>{birthday.name}</h3>
          </a>
        </div>
      );
    };

export default BirthdayWish;